import React, { useEffect } from 'react'
import register from '../../api/register'
import { useAlert } from 'react-alert'
import { Form, Input, Button, Row, Col, Select, DatePicker } from 'antd';
import btn_left from '../../asset/Button_left.png';
import btn_right from '../../asset/Button_right.png';

const { Option } = Select

function StepForm1(props) {

    const { currentStep, handlePrev, handleNext, user, summary } = props
    
    const alert = useAlert()
    
    useEffect(() => {
        if(user.hasOwnProperty("general")) {
            let generalData = user.general
            console.log("gen ", generalData.birthday)
            delete generalData._id
            delete generalData.birthday
            props.form.setFieldsValue(generalData)
        }
    }, []);
    
    const nextStep = async (payload) => {
        console.log("payload ", payload)
        const flag = await register.sendData(currentStep, payload)
        console.log("Click Next")
        console.log(flag)
        if (flag) {
            console.log("Next 2")
            handleNext()
            alert.success('')
        } else {
            alert.error('')
        }
    }

    const handleSubmit = e => {
        e.preventDefault();
        props.form.validateFields((err, values) => {
          if (!err) {
            console.log('Received values of form: ', values)
            console.log('birthday', values['birthday'])
            nextStep({
                ...values,
                birthday: values['birthday'].format('YYYY-MM-DD')
            })
          }
        });
      };

    const { getFieldDecorator } = props.form;

    return (
        <div>
            <h1></h1>
            <Form onSubmit={handleSubmit} >
                <Form.Item label="">
                {getFieldDecorator('name_th', {
                    rules: [{ required: true, message: '' }],
                })(
                    <Input
                    placeholder="  "
                    disabled={summary}
                    />,
                )}
                </Form.Item>

                <Form.Item label="">
                {getFieldDecorator('name_eng', {
                    rules: [{ required: true, message: '' }],
                })(
                    <Input
                    placeholder="Somchai  Jaidee"
                    disabled={summary}
                    />,
                )}
                </Form.Item>

                <Row>
                    <Col span={4}>
                        <Form.Item label="">
                        {getFieldDecorator('nickname', {
                            rules: [{ required: true, message: '' }],
                        })(
                            <Input
                            placeholder=""
                            disabled={summary}
                            />,
                        )}
                        </Form.Item>
                    </Col>
                    <Col span={4} offset={1}>
                        <Form.Item label="">
                        {getFieldDecorator('sex', {
                            rules: [{ required: true, message: '', enum: ['', '']}],
                        })(
                            <Select disabled={summary}>
                                <Option value=""></Option>
                                <Option value=""></Option>
                            </Select>
                            ,
                        )}
                        </Form.Item>
                    </Col>
                    <Col span={4} offset={1}>
                        <Form.Item label="">
                        {getFieldDecorator('birthday', {
                            rules: [{ type: 'object', required: true, message: '' }],
                        })(<DatePicker disabled={summary}/>)}
                        </Form.Item>
                    </Col>
                    <Col span={4}>
                        <Form.Item label="">
                        {getFieldDecorator('bloodenum', {
                            rules: [{ required: true, message: '', enum: ['A', 'B', 'AB', 'O']}],
                        })(
                            <Select disabled={summary}>
                                <Option value="A">A</Option>
                                <Option value="B">B</Option>
                                <Option value="AB">AB</Option>
                                <Option value="O">O</Option>
                            </Select>
                            ,
                        )}
                        </Form.Item>
                    </Col>
                    <Col span={4} offset={1}>
                        <Form.Item label="">
                        {getFieldDecorator('religion', {
                            rules: [{ required: true, message: '' }],
                        })(
                            <Input
                            placeholder=""
                            disabled={summary}
                            />,
                        )}
                        </Form.Item>
                    </Col>
                </Row>

                <Row>
                    <Col span={4}>
                        <Form.Item label="">
                        {getFieldDecorator('shirt_size', {
                            rules: [{ required: true, message: '', enum: ['S', 'M', 'L', 'XL']}],
                        })(
                            <Select disabled={summary}>
                                <Option value="S">S</Option>
                                <Option value="M">M</Option>
                                <Option value="L">L</Option>
                                <Option value="XL">XL</Option>
                            </Select>
                            ,
                        )}
                        </Form.Item>
                    </Col>

                    <Col span={4} offset={1}>
                        <Form.Item label="">
                        {getFieldDecorator('telephone', {
                            rules: [{ required: true, message: ' 10 ', len:10}],
                        })(
                            <Input
                            disabled={summary}
                            placeholder="0903234466"
                            />,
                        )}
                        </Form.Item>
                    </Col>
                    <Col span={6} offset={1}>
                        <Form.Item label="E-mail">
                        {getFieldDecorator('email', {
                            rules: [{ required: true, message: ' 10 ', type: 'email'}],
                        })(
                            <Input
                            disabled={summary}
                            placeholder="comcamp32@gmail.com"
                            />,
                        )}
                        </Form.Item>
                    </Col>
                </Row>
                
                {!summary && 
                <Form.Item>
                    <div class="Button-Row">
                    <div className="Button-Column right">
                                <div className="Button-Left-Image">
                                    <img
                                        src={btn_left}
                                        alt="Left button decoration"
                                    />
                                </div>
                                <div className="Button-Right-Image">
                                    <img
                                        src={btn_right}
                                        alt="Right button decoration"
                                    />
                                </div>
                                <div className="Button-BorderImage"></div>
                                <button className="Button-Background" htmlType="submit">
                                    <span className="Markdown">Next</span>
                                </button>
                            </div>

                            <div className="Button-Column left">
                                <div className="Button-Left-Image">
                                    <img
                                        src={btn_left}
                                        alt="Left button decoration"
                                    />
                                </div>
                                <div className="Button-Right-Image">
                                    <img
                                        src={btn_right}
                                        alt="Right button decoration"
                                    />
                                </div>
                                <div className="Button-BorderImage"></div>
                                <button className="Button-Background" onClick={handlePrev}>
                                    <span className="Markdown">Back</span>
                                </button>
                            </div>
                        </div>
                </Form.Item>
                }
            </Form>
        </div>
    )
}

export default Form.create({name: 'step1'})(StepForm1)